import React from 'react'
import { useRef, useEffect, useState } from 'react';
import { post } from './Networkcall';


function Login({ setLoginRef }) {

  const inputRef = useRef(null)
  const [number, setNumber] = useState('');
  const [pwd, setPwd] = useState('');
  const [success, setSuccess] = useState(-1);

  useEffect(() => {
    inputRef.current.focus();
    setLoginRef(Login);
  }, [])

  const submitHandler = (event) => {
    event.preventDefault();
    post('', {
      number: number,
      password: pwd
    }
    ).then(data => {
      console.log();
      setSuccess(1);
      //href...new page....
    })
      .catch(error => {
        console.log(error);
      });
  }

  return (
    <>
      <div class="mx-auto col-md-6 col-10 bg-white p-5" ref={(section) => { Login = section; }} >
        <h1 class="mb-4">Log in</h1>
        <form onSubmit={submitHandler} >
          <div class="form-group"> <input ref={inputRef} onChange={e => setNumber(e.target.value)} type="text" class="form-control" placeholder="Enter Your Number" id="form9" /> </div>
          <div class="form-group mb-3"> <input type="password" onChange={e => setPwd(e.target.value)} class="form-control" placeholder="Password" id="form10" /> <small class="form-text text-muted text-right">
            <a> Recover password</a>
          </small> </div> <button type="submit" class="btn btn-primary">Submit</button>
        </form>
      </div>
    </>
  )
};

export default Login
