import React from 'react';
 import './Button.css';
import dot from './dot.png';
import styled from 'styled-components';
import Routes from './Routes';
import Tooltip from '@material-ui/core/Tooltip';
import Button from '@material-ui/core/Button';
import { Link, Element , Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';


const ImageOverlay = styled.div`
    @media all and (pointer: coarse) {
    display:none
}
`;

const lam = createMuiTheme({
  palette: {
    primary: {
      // light: will be calculated from palette.primary.main,
      main: '#FAD961',
      // dark: will be calculated from palette.primary.main,
      // contrastText: will be calculated to contrast with palette.primary.main
    },
  }
});

function button (){
  const LightTooltip = withStyles(theme => ({
    tooltip: {
      backgroundColor: lam.palette.primary.main,
      color: 'black',
      boxShadow: 'none',
      fontSize: 15,
    },
  }))(Tooltip);

    return (
      <>
        <ImageOverlay>
        {Routes.map((prop, index) => (
        <Link activeClass="active" className={prop.sidebarName} to={prop.id} spy={true} smooth={true} duration={500} >
          <LightTooltip title={prop.sidebarName} placement="left">
            <div className={prop.classname}>
              </div></LightTooltip></Link>
        ))}
        </ImageOverlay>
      </>
    )
}

export default button
