import React from 'react';
import {useEffect,useState,useRef} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail';
import scrollToComponent from 'react-scroll-to-component';
import Routes from './Routes';
import { Link, DirectLink, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll';
import MenuItem from '@material-ui/core/MenuItem';
import MenuList from '@material-ui/core/MenuList';
import button from './Button';
import styled from 'styled-components';

import { createMuiTheme } from '@material-ui/core/styles';

const lam = createMuiTheme({
  palette: {
    primary: {
      // light: will be calculated from palette.primary.main,
      main: '#FAD961',
      // dark: will be calculated from palette.primary.main,
      // contrastText: will be calculated to contrast with palette.primary.main
    },
  }
});

const ImageOverlay = styled.div`
    @media all and (pointer: coarse) {
    background:lam.palette.primary.main,   
}
`;


const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
  },
  list: {
    width: 250,
  },
  menuButton: {
    marginRight: theme.spacing(-3),
  },
  edgeStart:{
    marginLeft:-40,
    '$sizeSmall&': {
      marginLeft: -1,
    },
  },
  title: {
    flexGrow: 1,
    marginLeft:-20,
    '$sizeSmall&': {
      marginLeft: 2,
    },
  },
}));


function Navbar(props) {

  const [state, setState] = React.useState({
    left: false,
    right: false,
  });

  const toggleDrawer = (side, open) => event => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [side]: open });
  };


  const sideList = side => (
    <div
      className={classes.list}
      role="presentation"
      onClick={toggleDrawer(side, false)}
      onKeyDown={toggleDrawer(side, false)}
    >

      <MenuList>
        {Routes.map((prop, index) => (
      <MenuItem component={Link} to={prop.id} spy={true} smooth={true} duration={500}>{prop.sidebarName}</MenuItem>
        ))}
      </MenuList>
    </div>
  );

  const classes = useStyles();

  return (
    <div className={classes.root}>
      {/* <AppBar position="fixed" color ="primary" style={{backgroundColor: "transparent"}}> */}
      {/* <AppBar position="fixed" > */}
      <ImageOverlay>
      <AppBar position="fixed" style={{backgroundColor:"transparent" ,boxShadow: 'none'}}>
        <Toolbar>
          <IconButton edge="start" className={classes.menuButton ,classes.edgeStart} color="inherit" aria-label="menu">
            <Button onClick={toggleDrawer('left', true)}><MenuIcon /></Button>
          </IconButton>
          <Drawer open={state.left} onClose={toggleDrawer('left', false)}>
            {sideList('left')}
          </Drawer>
          <Typography variant="h6" className={classes.title} style={{color:"black"}}>
            HotShot Adz
            </Typography>
          {/* <Button>Login</Button>
          <Button>Signin</Button> */}
        </Toolbar>
      </AppBar>
      </ImageOverlay>
    </div>
  );
}

export default Navbar
